
import { defineComponent, toRef } from "vue";
import { useModelValue } from "@/composables/useModelValue";

export default defineComponent({
  inheritAttrs: false,
  props: {
    label: {
      type: String,
      required: true
    },
    modelValue: {
      type: [Number, String],
      required: true
    }
  },
  emits: ["update:modelValue"],
  setup(props, context) {
    return useModelValue(toRef(props, "modelValue"), context);
  }
});
